<template>
  <div class="login">
    <div class="head">
      <img class="logo" src="../../static/img/POS.png" alt />
    </div>
    <div class="content">
      <div class="nav">
        <!-- <div @click="nav(0)" :class="{'active':index==0}">登录</div> -->
        <div class="active">注册</div>
      </div>
      <main>
        <div class="registerWrap">
          <div class="box">
            <img src="../../static/img/lever.png" alt />
            <input type="text" placeholder="请输入邀请码" v-model.trim="lever" />
          </div>
          <div class="box">
            <img src="../../static/img/phone.png" alt />
            <input type="number" placeholder="请输入手机号码" v-model.trim="phone" />
          </div>
          <div class="box boxCode">
            <img src="../../static/img/yanzheng.png" alt />
            <input type="number" placeholder="请输入验证码" v-model.trim="phcode" />
            <div class="code" v-if="daojishi">剩余{{num}}s</div>
            <div class="code" @click="getCode" v-else>获取验证码</div>
          </div>
          <div class="box">
            <img src="../../static/img/psd.png" alt />
            <input type="password" placeholder="请输入密码" v-model.trim="psd" />
          </div>
          <div class="box">
            <img src="../../static/img/psd.png" alt />
            <input type="password" placeholder="请再次确认密码" v-model.trim="repsd" />
          </div>
          <div class="btn" @click="regis">注册</div>
        </div>
      </main>
    </div>
  </div>
</template>

<script>
export default {
  props: {},
  data() {
    return {
      index: 1,
      phone: "",
      phcode: "",
      loginpsd: "", //登录密码
      psd: "",
      repsd: "",
      lever: "", //邀请码
      lever1: "请输入邀请码", //邀请码
      num: 60, //倒计时
      daojishi: false
    };
  },
  computed: {},
  created() {
    this.index = this.$route.query.index || 0;
    this.lever = this.$route.query.invite_code;
  },
  mounted() {},
  watch: {},
  methods: {
    getCode() {
      //获取验证码
      let mobile = this.phone.match(/^[1][3456789][0-9]{9}$/)
        ? this.phone
        : false;
      if (this.phone == "") {
        this.$toast("请输入手机号码");
      } else if (!mobile) {
        this.$toast("手机号码不规范");
      } else {
        this.$post("/login/code", {
          mobile: this.phone,
          invite_code: this.lever
        }).then(res => {
          console.log(res);
          if (res.code == 100) {
            this.$toast(res.msg);
            this.daojishi = true;
            this.timer = setInterval(() => {
              this.num--;
              if (this.num <= 0) {
                this.daojishi = false;
                this.num = 60;
                clearInterval(this.timer);
              }
            }, 1000);
          } else {
            this.$toast(res.msg);
          }
        });
      }
    },
    regis() {
      let that = this;
      if (this.phcode == "") {
        this.$toast("请输入手机验证码");
      } else if (this.psd == "") {
        this.$toast("请输入密码");
      } else if (this.repsd == "") {
        this.$toast("请再次输入密码");
      } else {
        this.$post("/login/appLogin", {
          mobile: this.phone,
          password: this.psd,
          code: this.phcode,
          passwordad: this.repsd,
          invite_code: this.lever
        }).then(res => {
          if (res.code == 100) {
            this.$toast(res.msg);
            setTimeout(function() {
              window.open("http://www.umihuoban.com/app.html");
            }, 2000);
          } else {
            this.$toast(res.msg);
          }
        });
      }
    }
  },
  components: {}
};
</script>

